@import url(https://fonts.googleapis.com/css?family=Caveat&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Tile {
    -webkit-flex-basis: calc(100% / 9);
            flex-basis: calc(100% / 9);
    border: 1px solid black;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.Tile::after {
    content: "";
    padding-bottom: 100%;
    display: block;
}

.Tile:nth-child(3n):not(:nth-child(9n)){
    border-right: 3px solid #000;
}
  
.Tile:nth-child(n+19):nth-child(-n+27),  
.Tile:nth-child(n+46):nth-child(-n+54) {
    border-bottom: 3px solid #000;
}

.Tile__Input {
    text-align: center;
    padding: 0;
    margin: 0;
    border: none;
    height: calc(100% - 2px);
    width: calc(100% - 2px);
    font-size: 5vmin;
    line-height: 6vmin;
    font-weight: bold;
    background-color: transparent;
    font-family: 'Caveat', cursive;
    color: #111;
}

.Tile__Input:focus {
    outline: none;
}

.Tile--Locked .Tile__Input {
    color: #333;
    font-family: inherit;
    background-color:  #f2f2f2;
    width: 100%;
    height: 100%;
}
.Tile--Correct {
    background-color: #64dd17;
}
.Tile--Wrong {
    background-color: #dd2c00;
}


/* Deleting arrows in HTML5 input */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance:textfield;
}
.Board {
    min-width: 360px;
}

.Grid {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    width: 90%;
    margin: 0 auto;
}

.Buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 20px;
}

.save-alert {
    font-weight: bold;
    margin: 0;
    margin-top: 10px;
}

.save-alert__hidden {
    color: transparent;
}
.Button {
	box-sizing: border-box;
	margin: 3px;
	padding: 10px 10px;
	min-width: 120px;
	font-size: 16px;
	font-weight: bold;
	cursor: pointer;
	background-color: inherit;
	background-position: 100% 0;
	background-size: 300%;
	border: 1px solid;
	border-radius: 3px;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    color: #111;
	border-color: #333;
    background-image: -webkit-linear-gradient(315deg, #222 50%, transparent 50%);
    background-image: linear-gradient(135deg, #222 50%, transparent 50%);
}
.Button:nth-child(3n){
    background-image: -webkit-linear-gradient(45deg, #222 50%, transparent 50%);
    background-image: linear-gradient(45deg, #222 50%, transparent 50%);
}
.Button:nth-child(2n){
    background-image: -webkit-linear-gradient(left, #222 50%, transparent 50%);
    background-image: linear-gradient(90deg, #222 50%, transparent 50%);
}
.Button:focus {
	outline: none;
}
.Button:hover,
.Button--Checked {
	background-position: 0 100%;
	color: white;
}
.Button--Disabled,
.Button--Disabled:hover {
	background-position: 100% 100%;
	cursor: inherit;
	color: #333;
    background-color:  #f2f2f2;
}

.App {
  width: 100%;
  max-width: 90vmin;
  min-height: 100vh;
  margin-left: auto;
  margin-right: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.App h1 {
  font-size: 7.5vmin;
  margin: 1vmin;
}

.App h2 {
  font-size: 30px;
  margin: 1vmin;
}

.ButtonGroup {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.load {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 5vmin 0;
}

.form__label {
    margin: 10px;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
}

.form__alert {
    margin: 0;
}
.form__alert--correct {
    color: #64dd17;
}
.form__alert--wrong {
    color: #dd2c00;
}

.form__input {
    box-sizing: border-box;
    min-width: 20vmin;
    height: 32px;
    font-size: 16px;
    border: 1px solid;
    border-radius: 3px;
    background: inherit;
    text-align: start;
    padding: 5px;
}

.form__submit {
    min-width: 15vmin;
    height: 32px;
    padding: 0;
    margin: 0;
    margin-left: 3px;
}

.form__input:focus,
.form__input:active {
    outline: none;
    background: transparent;
}
